.service-shape .our-service-shape {
  position: absolute;
  bottom: 380px;
  right: 80px;
  z-index: -1;
  -webkit-animation-name: rotateme;
  animation-name: rotateme;
  -webkit-animation-duration: 20s;
  animation-duration: 20s;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
}

/* Our Services Area */
.our-services-area {
  background-color: #b9b8b921;
}

.our-service-card {
  padding: 40px 30px;
  /* background: rgba(214, 214, 214, 0.2); */
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  margin-bottom: 10rem;
  border-radius: 10px;
  text-align: center;
  transition: all 0.5s;
  height: 400px;
}
.our-service-card img {
  background-color: #f2e88d;
  width: 130px;
  height: 130px;
  border-radius: 4px;
  top: -91px;
  position: relative;
}

.our-service-card i {
  font-size: 30px;
  width: 55px;
  height: 55px;
  line-height: 55px;
  border-radius: 5px;
  text-align: center;
  margin-bottom: 25px;
  color: var(--background);
}

.our-service-card i i {
  position: relative;
  left: -15px;
  transition: all 0.5s;
}

.our-service-card h3 {
  font-size: 22px;
  margin-bottom: 15px;
}

.our-service-card:hover {
  margin-top: -5px;
  box-shadow: 0px 8px 20px 0px #b9b8b9;
  -webkit-animation: CardHover 5s linear;
          animation: CardHover 5s linear;
}
.our-service-card p {
  color: #111;
}
@-webkit-keyframes CardHover {
  from {
    background-image: linear-gradient(
      0deg,
      rgb(73, 180, 252),
      rgb(241, 235, 144)
    );
  }
  to {
    background-image: linear-gradient(
      0deg,
      rgb(73, 180, 252),
      rgb(241, 235, 144)
    );
  }
}
@keyframes CardHover {
  from {
    background-image: linear-gradient(
      0deg,
      rgb(73, 180, 252),
      rgb(241, 235, 144)
    );
  }
  to {
    background-image: linear-gradient(
      0deg,
      rgb(73, 180, 252),
      rgb(241, 235, 144)
    );
  }
}

